<template>
  <div>
    <footer  class="myFooter">
      <div @click="showList">
      <span :class="{imgCont:elBadge, animated:elBadge, bigChage:anima}" ref="imgChag">
          <img :src="imgSrc" alt="小餐车" :class="elBNum?'blue':'gray'">
        </span>
      <el-badge :value="elBNum" class="item" :hidden="!elBNum" />
      <span class="info">
        <span :class="foodMon?'hasfoodMon':'foodMon'">{{getFoodMon}}</span>
        <span class="peisongfei">另需配送费1.5元</span>
      </span>
      </div>
      <span :class="foodMon<20?'secuBtn':'buyBtn'">{{getSecuBtn}}</span>
    </footer>
    <transition name="fade">
      <div class="weimu" v-show="isClickG" @click="removeWeimu"></div>
    </transition>
    <transition name="topUp">
      <div class="foodList" v-show="isClickG">
        <div class="listTitle">
          <div>已选商品</div>
          <div class="clearList" @click="clearList"><i class="el-icon-delete"></i>清空</div>
        </div>
        <div class="listItem" v-for="(fItem, fKey) in foodArr" :key="fKey">
          <div class="leDiv">
            <span class="fTtemName">{{fItem.name}}</span>
          </div>
          <div class="riBtn">
            <span class="fItemMon">￥{{fItem.price * foodAddOne[fKey]}}</span>
            <i class="el-icon-remove" @click="itemdelOneNum(fKey)"></i>
            <span class="fCloun">{{foodAddOne[fKey]}}</span>
            <i class="el-icon-circle-plus-outline" @click="itemAddOneNum(fKey)"></i>
          </div>
        </div>
      </div>
    </transition>
  </div>
</template>

<script>
  import phone from '../phone/phone.js';
  import huiImg from '../../../resource/img/gouhui.png';
  import lanImg from '../../../resource/img/gouwu.png';
  export default {
    name: 'myFooter',
    data () {
      return {
        elBadge: true, // 默认false显示徽章
        elBNum: 0, // 徽章显示个数
        huiImg,
        lanImg,
        anima: false,
        foodMon: 0,
        isClickG: false,
        foodArr: [], // 食物详细清单
        foodBianHao: [], // 食物编号数组,用索引确定是否重复,哪个列表上加1
        foodAddOne: [] // 食物加1数组
      };
    },
    mounted () {
      phone.$on('sendMoTx', (data, bianhao) => {
        this.elBNum++;
        this.foodMon += data.price;
        if (!this.foodBianHao.includes(bianhao)) {
          this.foodArr.push(data);
          this.foodBianHao.push(bianhao);
          this.foodAddOne.push(1);
        } else {
          var i = this.foodBianHao.indexOf(bianhao);
          this.foodAddOne.splice(i, 1, this.foodAddOne[i] + 1);
        }
      });
      phone.$on('sendDelOne', (delOne, foodsI) => {
        this.elBNum--;
        this.foodMon -= foodsI.price;
        var i = this.foodBianHao.indexOf(delOne);
        if (this.foodAddOne[i] > 1) {
          this.foodAddOne.splice(i, 1, this.foodAddOne[i] - 1);
        } else if (this.foodAddOne[i] === 1) {
          this.foodBianHao.splice(i, 1);
          this.foodAddOne.splice(i, 1);
          this.foodArr.splice(i, 1);
        }
      });
      // 监听点击了购物车时上拉列表进行的相关操作
      phone.$on('foodTomyfooterAddOne', (foodData, num) => {
        this.elBNum++;
        this.foodMon += foodData.price;
        if (!this.foodBianHao.includes(num)) {
          this.foodArr.push(foodData);
          this.foodBianHao.push(num);
          this.foodAddOne.push(1);
        } else {
          var i = this.foodBianHao.indexOf(num);
          this.foodAddOne.splice(i, 1, this.foodAddOne[i] + 1);
        }
      });
    },
    computed: {
      imgSrc: function () {
          if (this.elBNum) {
            return lanImg;
          } else {
            return huiImg;
          }
        },
      getFoodMon: function () {
        if (this.foodMon) {
          return '￥' + this.foodMon;
        } else {
          return '未选购商品';
        }
      },
      getSecuBtn: function () {
        if (this.foodMon) {
          if (this.foodMon < 20) {
            return '还差￥' + (20 - this.foodMon) + '起送';
          } else {
            return '去结算';
          }
        } else {
          return '￥20起送';
        }
      }
    },
    methods: {
      // 不断改变动画类的赋值和去值
      changeAnima () {
        this.anima = true;
        setTimeout(() => {
          this.anima = false;
        }, 500);
      },
      showList () {
        if (this.foodMon) {
          this.isClickG = !this.isClickG;
        }
      },
      removeWeimu () {
        if (this.isClickG) {
          this.isClickG = !this.isClickG;
        }
      },
      clearList () {
        this.foodArr = [];
        this.foodBianHao = [];
        this.foodAddOne = [];
        this.elBNum = 0;
        this.foodMon = 0;
        phone.$emit('sendClear', 0);
        this.isClickG = false;
      },
      itemAddOneNum (k) {
        this.elBNum++;
        this.foodMon += this.foodArr[k].price;
        this.foodAddOne.splice(k, 1, this.foodAddOne[k] + 1); // 本上拉列表对应项加1
        var sendIndex = this.foodBianHao[k];
        phone.$emit('itemAddOne', sendIndex); // 通知goods组件对应的食物列表上加1
      },
      itemdelOneNum (k) {
        // 上拉列表项减1函数
        this.elBNum--;
        this.foodMon -= this.foodArr[k].price;
        // 通知goods组件对应的食物列表项上作减1操作
        var sendDelIndex = this.foodBianHao[k];
        phone.$emit('sendGoodsDelOne', sendDelIndex);
        if (this.foodAddOne[k] > 1) {
          this.foodAddOne.splice(k, 1, this.foodAddOne[k] - 1);
        } else if (this.foodAddOne[k] === 1) {
          // 注意通知减1操作必须放在编号数组减1之前,否则下标越界!
          this.foodBianHao.splice(k, 1);
          this.foodAddOne.splice(k, 1);
          this.foodArr.splice(k, 1);
        }
      }
    },
    watch: {
      elBNum: function (nElnum, oElnum) {
        if (nElnum > oElnum) {
          this.changeAnima();
        } else if (nElnum === 0) {
          // 如果徽章图标数量为0,则自动关闭帷幕
          this.isClickG = false;
        }
      }
    }
  };
</script>

<style scoped lang="less">
  @rpx:0.027rem;
  .myFooter{
    position: fixed;
    width: 100%;
    background: hsl(290,5%,30%);
    text-align: center;
    bottom: 0px;
    height: 45px;
    z-index: 100;
  }
  .imgCont{
    display: inline-block;
    width: 100*@rpx;
    height: 100*@rpx;
    position: absolute;
    top:-15px;
    left:15px;
    text-align: center;
    img{
      width: 100%;
      height: 100%;
      border-radius: 100%;
      border:8px solid hsl(290,5%,25%);
    }
  }
  .blue{
    background: #00ffff;
  }
  .gray{
    background: #ccc;
  }
  .item{
    position: absolute;
    top:-12px;
    left: 18px;
  }
  footer{
    height: 100%;
  }
  .info{
    width: 300*@rpx;
    height: 100%;
    display: flex;
    flex-direction: column;
    font-size: x-small;
    margin-left: 150*@rpx;
    padding: 5px 0;
  }
  .secuBtn{
    display: inline-block;
    width: 300*@rpx;
    height: 100%;
    background: hsl(290,5%,35%);
    position: absolute;
    right: 0;
    top: 0;
    padding-top: 15px;
  }
  .buyBtn{
    display: inline-block;
    width: 300*@rpx;
    height: 100%;
    background: hsl(120,90%,68%);
    color:white;
    font-size: large;
    position: absolute;
    right: 0;
    top: 0;
    padding-top: 15px;
  }
  .foodMon{
    padding-bottom: 5px;
  }
  .hasfoodMon{
    padding-bottom: 5px;
    font-size: larger;
    color:#f8f8ff;
  }
  .peisongfei{
      font-size: smaller;
    }
    .bigChage{
      animation: heartBeat 0.7s ease;
    }
    .weimu{
      display: block;
      position: absolute;
      top:0;
      left: 0;
      right: 0;
      bottom: 0;
      z-index: 80;
      background: hsla(200,20%,55%,0.4);
    }
    .foodList{
      z-index: 90;
      width: 100%;
      padding-bottom: 20px;
      font-size: 16px;
      background: #ccc;
      position: fixed;
      bottom: 45px;
      left: 0;
      right: 0;
      box-sizing: border-box;
      .listTitle{
        display: flex;
        flex-direction: row;
        justify-content: space-between;
        align-items: center;
        box-sizing: border-box;
        padding: 15px;
        font-size: small;
      }
    }
    .listItem{
      font-size: larger;
      background: rgba(255,255,255,0.8);
      padding: 15px;
      display: flex;
      flex-direction: row;
      justify-content: space-between;
    }
    .fItemMon{
      display: inline-block;
      padding-right: 10px;
      color:#ff4500;
      font-weight: bold;
    }
    .riBtn{
      i{
        color: #4169e1;
      }
    }
    .topUp-enter-active{
      animation: slideInUp 0.7s ease;
    }
    .topUp-leave-active{
      animation: slideOutDown 0.7s ease;
    }
  .fade-enter-active{
    animation: fadeIn 0.6s ease;
  }
  .fade-leave-active{
    animation: fadeOut 0.5s ease;
  }
</style>



