<template>
  <div class="myHeader">
    <header>
      <div class="content">
        <div class="img_content">
          <img src="../../../resource/img/goodsTitle.png" alt="店家招牌">
        </div>
        <div class="detail_content">
          <div class="title"><span class="brand"><img src="../../../resource/img/brand@2x.png" alt="brand"></span>一家小店(红运村)</div>
          <div class="time">蜂鸟专送 38分钟送达</div>
          <div class="money"><span class="jian"><img src="../../../resource/img/jian.png" alt=""></span><span class="zaixian">在线支付满28减5,满50减10</span></div>
        </div>
        <div class="number" @click="showDetail">5个<span class="right_icon"></span></div>
      </div>
      <div class="notice" @click="showDetail">
        <span class="notice_icon">
          <img src="../../../resource/img/gonggao.png" alt="">
        </span>
        <span>
          {{detailData}}
        </span>
      </div>
      <!-- 这里是高贵的模糊背景 -->
      <div class="background"></div>
      <transition name="maskAnim">
        <div class="detailMask" v-show="detailFlag">
          <div class="detail-wrap">
            <div class="detail-main">
              <div class="detail-main-title">一家小店(红运村)</div>
              <el-rate
              class="star"
              v-model="value"
              disabled
              show-score
              text-color="#ff9900"
              score-template="{value}">
            </el-rate>
            <div class="youhui">
              <span class="left_l"></span><span class="youhui_con">优惠信息</span><span class="right_l"></span>
              <div><span class="jian"><img src="../../../resource/img/jian.png" alt=""></span>在线支付满28减5,满50减10</div>
              <div><span class="jian"><img src="../../../resource/img/zhe.png" alt=""></span>单人精彩赛</div>
              <div><span class="jian"><img src="../../../resource/img/zhe.png" alt=""></span>清肺雪梨汤8折抢购</div>
              <div><span class="jian"><img src="../../../resource/img/te.png" alt=""></span>特价饮品8折抢购</div>
              <div><span class="jian"><img src="../../../resource/img/te.png" alt=""></span>单人特色套餐</div>
            </div>
            <div class="business_GAO">
              <span class="left_l"></span><span class="youhui_con">商家公告</span><span class="right_l"></span>
              <div>{{detailData}}</div>
            </div>
            </div>
          </div>
          <div class="detail-close"><span @click="detailFlag=false">&times;</span></div>
        </div>
      </transition>
    </header>
  </div>
</template>

<script>
export default {
  name: 'myHeader',
  data () {
    return {
      detailData: '关于五味粥的来历，我县还流传着这样一个故事：元朝末年，朱元璋部将陈祥率领农民起义军自临海攻打天台城，在东横岭设下营寨。天台百姓痛恨元人的残酷野蛮的统治，纷纷响应，主动帮助义军探听军情，筹粮运草。元至正廿七年(1367)年底，义军包围县城，与城里居民约定于除夕之夜，里应外合，聚歼元兵。时值雪后，城外积雪足有尺把厚。守将乌黑尔烈估计义军不会攻城，自去衙门饮酒作乐。到了半夜，三声炮响，杀声四起。义军除掉守兵，打开城门，经过一场激战，杀死守将，占领了县城。',
      detailFlag: false,
      value: 3.8
    };
  },
  methods: {
    showDetail () {
      this.detailFlag = true;
    }
  }
};
</script>

<style scoped lang="less">
 @rpx:0.027rem;
  .myHeader{
    background: rgba(7,17,27,0.5);
    position: relative;
  }
  .img_content{
    width: 200*@rpx;
    height: 200*@rpx;
    background: yellow;
    img{
      width: 100%;
      height: 100%;
    }
  }
  .content{
    color: cornsilk;
    display: flex;
    padding: 60*@rpx;
    padding-bottom: 0;
    margin-right: 30*@rpx;
    margin-bottom: 40*@rpx;
    align-items: flex-end;
    > div{
      flex: auto;
    }
    .detail_content{
      align-self: stretch;
      font-size: 12px;
      margin-left: 48*@rpx;
      display: flex;
      flex-direction: column;
      > div{
        flex:1;
      }
      .brand{
        display: inline-block;
        width: 60*@rpx;
        height: 28*@rpx;
        background: red;
        margin-right: 10px;
        img{
          width: 100%;
          height: 100%;
        }
      }
    }
  }
  .jian{
    display: inline-block;
    width: 40*@rpx;
    height: 40*@rpx;
    background: white;
    img{
      width: 100%;
      height: 100%;
    }
  }
  .number{
        font-size: 10px;
        color: white;
        background: rgba(7,17,27,0.2);
        padding: 5px;
        text-align: center;
        border-radius: 8px;
        &::after{
          margin-left: 3px;
          content: "";
          display: inline-block;
          width: 6px;
          height: 6px;
          border-right: 1px solid #fafad2;
          border-bottom: 1px solid #fafad2;
          -webkit-transform:rotate(-45deg);
          transform: rotate(-45deg);
        }
      }
      .zaixian{
        position: relative;
        top: -5px;
      }
      .notice{
        color: white;
        background: rgba(7,17,27,0.2);
        height: 70*@rpx;
        white-space: nowrap;
        overflow: hidden;
        text-overflow: ellipsis;
        line-height: 70*@rpx;
        font-size: 20*@rpx;
        padding: 6px;
        .notice_icon{
          display: inline-block;
          width: 80*@rpx;
          height: 40*@rpx;
          background: white;
          position: relative;
          top: 4px;
          margin-right: 8px;
          img{
            width: 100%;
            height: 100%;
          }
        }
      }
      .background{
        position: absolute;
        top: 0;
        right: 0;
        width: 100%;
        height: 100%;
        z-index: -1;
        background-image: url(../../../resource/img/goodsTitle.png);
        background-size: cover;
        background-repeat: no-repeat;
        filter: blur(10px);
      }
      .title{
        font-size: large;
        font-weight: bolder;
      }
      .detailMask{
        box-sizing: border-box;
        background: rgba(7,17,27,0.8);
        overflow: auto;
        position: fixed;
        width: 100%;
        height: 100%;
        z-index: 128;
        top: 0;
        left: 0;
        .detail-wrap{
          box-sizing: border-box;
          min-height: 100%;
          .detail-main{
            box-sizing: border-box;
            text-align: center;
            color: white;
            padding-top: 64px;
            margin-left: 80*@rpx;
            margin-right: 80*@rpx;
            padding-bottom: 64px;
          }
        }
      }
.detail-close{
    color: white;
    position: relative;
    width: 32px;
    height: 32px;
    margin: -64px auto 0 auto;
    clear: both;
    font-size: 32px;
    span{
      display: inline-block;
      width: 40px;
      height: 40px;
      border-radius: 100%;
      border:1px solid white;
      text-align: center;
      line-height: 40px;
    }
  }
  .youhui{
    div{
      text-align: left;
      margin: 20*@rpx auto;
    }
    .jian{
      margin-right: 20*@rpx;
    }
  }
  .detail-main-title{
    margin-bottom: 40*@rpx;
    font-size: larger;
  }
  .star{
    margin-bottom: 30*@rpx;
  }
  .left_l{
      display: inline-block;
      width: 28%;
      height: 0;
      border-top: 1px solid rgba(255,255,255,0.3);
      position: relative;
      bottom: 5px;
    }
    .right_l{
      display: inline-block;
      width: 28%;
      height: 0;
      border-top: 1px solid rgba(255,255,255,0.3);
      position: relative;
      bottom: 5px;
    }
    .youhui_con{
      padding: 0 30*@rpx;
      font-size: large;
    }
    .business_GAO{
      > div{
        line-height: 30px;
        margin-top: 30*@rpx;
        text-align: left;
      }
    }
    .maskAnim-enter-active{
      animation: fadeIn 0.7s ease;
    }
    .maskAnim-leave-active{
      animation: fadeOut 0.7s ease-in;
    }
</style>



