<template>
  <div class="evaluateCont" :style="{width: userWid, height: userHei}">
    <div class="zhonghe">
      <div class="NumPing">
        <div class="num">3.9</div>
        <p>综合评分</p>
        <p>高于周边商家78%</p>
      </div>
      <div class="starPing">
        <div class="atitul">
          <span>服务态度</span>
          <el-rate
          class="scaleIt"
          v-model="tvalue"
          disabled
          show-score
          text-color="#ff9900"
          score-template="3.9">
        </el-rate>
        </div>
        <div class="atitul">
          <span>口味评分</span>
          <el-rate
          class="scaleIt"
          v-model="kvalue"
          disabled
          show-score
          text-color="#ff9900"
          score-template="4.0">
          </el-rate>
        </div>
        <div class="daoTime">
          <span>送达时间</span><span>44分钟</span>
        </div>
      </div>
    </div>
    <div class="pingHeader">
      <div class="btnGrp">
        <el-button type="primary" plain checked>全部</el-button>
        <el-button plain>推荐</el-button>
        <el-button type="info" plain>吐槽</el-button>
      </div>
      <div class="choose">
        <i class="el-icon-success" @click="choosePJ($event)"></i>
        <span>只看有内容的评价</span>
      </div>
    </div>
    <div class="pingCont">
      <div class="pingList" v-for="(pingOne, pingK) in evaData.ratings" :key='pingK'>
        <div class="pingImg">
          <i class="el-icon-user-solid"></i>
        </div>
        <div class="pingRight">
          <div class="usrName">{{pingOne.username}}</div>
          <div class="starCon">
            <i class="star">
              <el-rate
                class="scaleIt"
                v-model="evaData.ratings[pingK].score"
                disabled
                text-color="#ff9900">
            </el-rate>
            </i><span class="timeDao" v-if="pingOne.deliveryTime">{{pingOne.deliveryTime}}分钟送达</span>
          </div>
          <div class="pingTxt">{{pingOne.text}}</div>
          <div class="hander">
            <i class="{'fa fa-thumbs-down': pingOne.rateType === 1,'fa fa-thumbs-up': pingOne.rateType === 0}"></i>
          </div>
        <div class="pingTime">{{pingOne.rateTime | myDate}}</div>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
  import evaData from './evaluate_data.js';
  export default {
    name: 'evaluate',
    data () {
      return {
        tvalue: 3.9,
        kvalue: 4.0,
        evaData: evaData,
        userWid: this.userW + 'px',
        userHei: (this.userH * 0.65) + 'px'
      };
    },
    methods: {
      choosePJ (event) {
        if (!this.choosePJFlag) {
          event.target.style.color = 'skyblue';
          this.choosePJFlag = !this.choosePJFlag;
        } else {
          event.target.style.color = 'black';
          this.choosePJFlag = !this.choosePJFlag;
        }
      }
    }
  };
</script>

<style scoped lang="less">
  @rpx:0.027rem;
  .zhonghe{
    box-sizing: border-box;
    display: flex;
    flex-direction: row;
    padding: 20px;
    background: white;
    width: 100%;
    margin-bottom: 25px;
    .NumPing{
      flex:3;
      text-align: center;
      padding-right: 20px;
      border-right: 0.5px solid #eee;
      .num{
        color: #ff9900;
        font-size: x-large;
        font-weight: bolder;
        padding-bottom: 10px;
      }
      p:first-of-type{
          font-size: large;
          line-height: 24px;
          color: black;
        }
    }
    .starPing{
      flex: 7;
      padding-left: 20px;
    }
  }
  .scaleIt{
    transform: scale(0.65,0.8);
  }
  .atitul{
    font-size: smaller;
    display: flex;
    flex-direction: row;
    padding: 10px;
  }
  .daoTime{
    padding: 10px;
    span:first-of-type{
      font-size: smaller;
      padding-right: 20px;
    }
    span:last-of-type{
      font-size: x-small;
      color: #808080;
    }
  }
  .pingHeader{
    background: white;
    padding: 15px;
    box-shadow: 4px 2px 5px #ccc;
    .btnGrp{
      padding-bottom: 15px;
    }
  }
  .choose{
    span{
      font-size: smaller;
      color: #ccc;
    }
  }
  .evaluateCont{
    box-sizing: border-box;
    width: 100%;
    overflow: auto;
    padding-bottom: 70px;
  }
  .pingCont{
    border-bottom: 65px;
    box-sizing: border-box;
    width: 100%;
    background: white;
  }
  .pingList{
    display: flex;
    flex-direction: row;
    justify-content: space-around;
    border-bottom: 1px solid #ccc;
    position: relative;
    padding-right: 10px;
    .pingImg{
      flex: 2;
      text-align: center;
    }
    .pingRight{
      flex: 8;
      display: flex;
      flex-direction: column;
      justify-content: space-between;
      align-items: flex-start;
    }
  }
  .starCon{
    position: relative;
    left: -25px;
    display: flex;
    flex-direction: row;
  }
  .pingTxt{
    font-size: x-small;
    padding-bottom: 15px;
  }
  .pingTime{
    position: absolute;
    right: 10px;
    top: 10px;
    font-size: xx-small;
    color: #ccc;
  }
  .timeDao{
    font-size: xx-small;
    color:#ccc;
  }
</style>



