<template>
  <transition name="fade">
      <div v-show="showFlag" class="food">
        <div class="foodHeader">
          <div class="headerImg">
            <img :src="foodData.image" alt="美食">
            <div class="back">
              <i class="el-icon-back iconAct" @click="goBack"></i>
            </div>
          </div>
          <div class="headerTxt">
            <div class="txtTitle">{{foodData.name}}</div>
            <div class="haoping">月售1666份,好评率100%</div>
            <div class="money">￥{{foodData.price}}</div>
            <el-button type="primary" class="elBtn" @click="addCar">加入购物车</el-button>
          </div>
        </div>
        <div class="foodInfo" v-if="foodData.info">
          <div class="infoTitle">商品介绍</div>
          <div class="infoCont">{{foodData.info}}</div>
        </div>
        <div class="pingjia">
          <div class="pingHeader">
            <div class="pTitle">商品评价</div>
            <div class="btnGrp">
              <el-button type="primary" plain checked>全部</el-button>
              <el-button plain>推荐</el-button>
              <el-button type="info" plain>吐槽</el-button>
            </div>
            <div class="choose">
              <i class="el-icon-success" @click="choosePJ($event)"></i>
              <span>只看有内容的评价</span>
            </div>
          </div>
          <hr>
          <div class="pingList" v-for="(pingV,pingK) in foodData.ratings" :key="pingK">
            <div class="timeAndPer clear">
              <div class="time">{{pingV.rateTime | myDate}}</div>
              <div class="person">
                <div class="perId">{{pingV.username}}</div>
                <div class="perImg">
                  <img :src="pingV.avatar" alt="" width="12" height="12">
                </div>
              </div>
            </div>
            <div class="pingOne">
              <i :class="{'fa fa-thumbs-down': pingV.rateType === 1,'fa fa-thumbs-up': pingV.rateType === 0}"></i>
              <span>{{pingV.text}}</span>
            </div>
            <hr class="pingLine">
          </div>
        </div>
      </div>
  </transition>
</template>

<script>
  import phone from '../phone/phone.js';
  export default {
    name: 'food',
    props: {
      foodData: {
        type: Object
      },
      sendItemKey: Number,
      sendfoodKey: Number
    },
    data () {
      return {
        showFlag: false,
        choosePJFlag: false,
        getPingList: []
      };
    },
    methods: {
      changeFlag () {
        this.showFlag = true;
      },
      goBack () {
        this.showFlag = false;
      },
      choosePJ (event) {
        if (!this.choosePJFlag) {
          event.target.style.color = 'skyblue';
          this.choosePJFlag = !this.choosePJFlag;
        } else {
          event.target.style.color = 'black';
          this.choosePJFlag = !this.choosePJFlag;
        }
      },
      addCar () {
        // 点击购物车时发送给两个组件的不同的相关数据
        phone.$emit('foodTogoodsAddOne', this.sendItemKey, this.sendfoodKey);
        var CarNum = this.sendItemKey + '' + this.sendfoodKey;
        phone.$emit('foodTomyfooterAddOne', this.foodData, CarNum);
      }
    }
  };
</script>

<style scoped lang="less">
  @rpx:0.027rem;
  .test{
    width: 100%;
    height: 300px;
    padding:30px;
    background: green;
  }
  .food{
    width: 100%;
    position: fixed;
    top: 0;
    left: 0;
    bottom: 40px;
    background: #eee;
    z-index: 50;
    overflow: auto;
  }
  .foodHeader{
    padding: 40*@rpx;
  }
  .fade-enter-active{
    animation: fadeInDown 0.7s ease;
  }
  .fade-leave-active{
    animation: fadeOutUp 0.7s ease;
  }
  .headerImg{
    position: relative;
    width: 100%;
    height: 0;
    padding-top: 100%;
    img{
      position: absolute;
      top: 0;
      left: 0;
      width: 100%;
      height: 100%;
    }
  }
  .back{
    position: absolute;
    top: 5px;
    left: 5px;
    .el-icon-back{
      color: white;
      font-size: 40*@rpx;
      display: block;
      padding: 5px;
      border:1px solid white;
      border-radius: 100%;
      z-index: 101;
    }
  }
  .headerTxt{
    box-sizing: border-box;
    width: 100%;
    height: 300*@rpx;
    background: white;
    position: relative;
    padding: 15px;
    box-shadow: 4px 2px 5px #ccc;
    .txtTitle{
      color: black;
      font-weight: bolder;
      font-size: larger;
      padding-bottom: 20*@rpx;
    }
    .haoping{
      color: #a9a9a9;
      padding-bottom:40*@rpx;
    }
    .money{
      color: red;
      font-weight: bolder;
    }
  }
  .elBtn{
    position: absolute;
    right: 10px;
    bottom: 10px;
  }
  .foodInfo{
    background: white;
     margin: 40*@rpx;
     padding: 20*@rpx;
     .infoTitle{
      font-size: larger;
      padding-bottom: 10px;
     }
     .infoCont{
      font-size: small;
      color: rgb(77,85,93);
      line-height: 40*@rpx;
     }
  }
  .pingjia{
    background: white;
    margin: 40*@rpx;
    padding: 20*@rpx;
    box-shadow: 4px 2px 5px #ccc;
    .pTitle{
      font-size: large;
      padding-bottom: 10px;
    }
    .btnGrp{
      padding: 15px 0;
    }
    .choose{
      padding-bottom:10px;
      span{
        color: #ccc;
        font-size: smaller;
      }
    }
  }
  .pingOne{
    .fa-thumbs-up{
      color: #8b0000;
    }
    .fa-thumbs-down{
      color: #a9a9a9;
    }
  }
  .pingLine{
    border:0.6px solid #d3d3d3;
  }
  .time{
    float: left;
  }
  .person{
    float: right;
  }
  .clear{
    &:after{
      content: '';
      display: block;
      clear: both;
    }
  }
</style>



