<template>
  <div class="tab">
    <nav>
      <div>
        <router-link :to="{name: 'goods'}" class="tab_item">商品</router-link>
      </div>
      <div>
        <router-link :to="{name: 'evaluate'}" class="tab_item">评价</router-link>
      </div>
      <div>
        <router-link :to="{name: 'business'}" class="tab_item">商家</router-link>
      </div>
    </nav>

    <router-view></router-view>
  </div>
</template>

<script>
  export default {
    name: 'tab'
  };
</script>

<style lang="less" scoped>
  @rpx:0.027rem;
  .tab{
    width: 100%;
    height: 100%;
    box-sizing: border-box;
  }
  .tab>nav{
    width: 100%;
    height: 150*@rpx;
    line-height: 150*@rpx;
    background: #eee;
    display: flex;
    flex-direction: row;
    justify-content: center;
    align-items: stretch;
  }
  .tab>nav>div{
    color: #2f4f4f;
    flex: 1;
    text-align: center;
    font-size: 50*@rpx;
    a{
      display: block;
    }
  }
  .routerActive{
    color: red;
    font-size: x-large;
  }
</style>



