<template>
  <div class="business" :style="{width: userWid, height: userHei}">
    <div class="busHeader">
      <div class="head1">
        <div class="busTit">一家小店(红运村)</div>
        <div class="busStar">
          <el-rate
            class="scaleIt"
            v-model="Svalue"
            disabled
            text-color="#ff9900">
          </el-rate>
          <span class="shou">月售666单</span>
        </div>
        <div class="heart">
          <div :class="{animated:true, rubberBand:rubberFlag}">
            <i :class="isHeart?'fa fa-heart':'fa fa-heart-o'" @click="heartClick"></i>
          </div>
          <div class="heartText" ref="heartTxt">收藏</div>
        </div>
      </div>
      <div class="head2">
        <div class="tLeft">
          <div class="shou">起送价</div>
          <div>20元</div>
        </div>
        <div class="tCenter">
          <div class="shou">商家配送</div>
          <div>4元</div>
        </div>
        <div class="tRight">
          <div class="shou">平均配送时间</div>
          <div>39分钟</div>
        </div>
      </div>
    </div>
    <div class="GonHuo">
      <div class="ghTitle">
        公告与活动
      </div>
      <div class="ghCont">
        一家小店坊其烹饪粥料的秘方源于中国千年古法，在融和现代制作工艺，由世界烹饪大师屈浩先生领衔研发。坚守纯天然、0添加的良心品质深得消费者青睐，发展至今成为粥类的引领品牌。是2008年奥运会和2013年园博会指定餐饮服务商。
      </div>
    </div>
    <div class="youhui">
      <div><span class="jian"><img src="../../../resource/img/jian.png" alt=""></span><span>在线支付满28减5</span></div>
      <div><span class="jian"><img src="../../../resource/img/zhe.png" alt=""></span><span>VC无限橙果汁全场8折</span></div>
      <div><span class="jian"><img src="../../../resource/img/zhe.png" alt=""></span><span>单人精彩套餐</span></div>
      <div><span class="jian"><img src="../../../resource/img/piao.png" alt=""></span><span>该商家支持发票,请下单写好发票抬头</span></div>
      <div><span class="jian"><img src="../../../resource/img/bao.png" alt=""></span><span>已加入“外卖保”计划,食品安全保障</span></div>
    </div>
    <div class="jingTit">店家实景</div>
    <div class="shijing">
      <div class="imgGrp">
        <div><el-image style="width: 240px; height: 180px" :src="url" :preview-src-list="srcList"></el-image></div>
        <div><el-image style="width: 240px; height: 180px" :src="url2" :preview-src-list="srcList"></el-image></div>
        <div><el-image style="width: 240px; height: 180px" :src="url3" :preview-src-list="srcList"></el-image></div>
        <div><el-image style="width: 240px; height: 180px" :src="url4" :preview-src-list="srcList"></el-image></div>
      </div>
    </div>
    <div class="imformation">
      <div class="imTitle">商家信息</div>
      <div class="imCont">
        <div>该商家支持发票,请下单写好发票抬头</div>
        <div>品类:其他菜系,包子粥店</div>
        <div>地球上的某个未知而神秘的地方...</div>
        <div>营业时间:10:00-20:30</div>
      </div>
    </div>
  </div>
</template>

<script>
  export default {
    name: 'business',
    data () {
      return {
        Svalue: 4, // 星星数量
        userWid: this.userW + 'px',
        userHei: (this.userH * 0.6) + 'px',
        url: 'https://timgsa.baidu.com/timg?image&quality=80&size=b9999_10000&sec=1568079857495&di=e2eb6e1d4b7002ebcadfaf9a33c7a286&imgtype=0&src=http%3A%2F%2Fimg4.duitang.com%2Fuploads%2Fblog%2F201404%2F29%2F20140429102153_2PXBf.thumb.600_0.jpeg',
        url2: 'https://timgsa.baidu.com/timg?image&quality=80&size=b9999_10000&sec=1568079857496&di=048d898a2674d5a170ab4d465b0fb760&imgtype=0&src=http%3A%2F%2Fb-ssl.duitang.com%2Fuploads%2Fblog%2F201404%2F26%2F20140426090619_CYweL.jpeg',
        url3: 'https://timgsa.baidu.com/timg?image&quality=80&size=b9999_10000&sec=1568079857497&di=2940bbbd297c2bed3e9b0b8bb946f313&imgtype=0&src=http%3A%2F%2Fimg4.duitang.com%2Fuploads%2Fblog%2F201404%2F26%2F20140426090614_FG5eL.jpeg',
        url4: 'https://timgsa.baidu.com/timg?image&quality=80&size=b9999_10000&sec=1568080385461&di=29aae158d76e0e83e2e70f221b587886&imgtype=0&src=http%3A%2F%2Fdimg04.c-ctrip.com%2Fimages%2Ffd%2Fhotel%2Fg3%2FM04%2F25%2F1A%2FCggYGVXvsmOANwskAAYq7YAMWik167_R_640_320.jpg',
        srcList: [
          'https://timgsa.baidu.com/timg?image&quality=80&size=b9999_10000&sec=1568079857495&di=e2eb6e1d4b7002ebcadfaf9a33c7a286&imgtype=0&src=http%3A%2F%2Fimg4.duitang.com%2Fuploads%2Fblog%2F201404%2F29%2F20140429102153_2PXBf.thumb.600_0.jpeg',
          'https://timgsa.baidu.com/timg?image&quality=80&size=b9999_10000&sec=1568079857496&di=048d898a2674d5a170ab4d465b0fb760&imgtype=0&src=http%3A%2F%2Fb-ssl.duitang.com%2Fuploads%2Fblog%2F201404%2F26%2F20140426090619_CYweL.jpeg',
          'https://timgsa.baidu.com/timg?image&quality=80&size=b9999_10000&sec=1568079857497&di=2940bbbd297c2bed3e9b0b8bb946f313&imgtype=0&src=http%3A%2F%2Fimg4.duitang.com%2Fuploads%2Fblog%2F201404%2F26%2F20140426090614_FG5eL.jpeg',
          'https://timgsa.baidu.com/timg?image&quality=80&size=b9999_10000&sec=1568080385461&di=29aae158d76e0e83e2e70f221b587886&imgtype=0&src=http%3A%2F%2Fdimg04.c-ctrip.com%2Fimages%2Ffd%2Fhotel%2Fg3%2FM04%2F25%2F1A%2FCggYGVXvsmOANwskAAYq7YAMWik167_R_640_320.jpg'
        ],
        isHeart: false, // 是否为空心状态,默认是空心
        rubberFlag: false, // 动画类切换标志
        isShouCang: -1 // 是否为可收藏状态
      };
    },
    methods: {
      heartClick () {
        this.isHeart = !this.isHeart;
        if (!this.isHeart) {
          this.$refs.heartTxt.textContent = '收藏';
        } else {
          this.$refs.heartTxt.textContent = '已收藏';
        }
        this.isShouCang *= -1;
        if (this.isShouCang === 1) {
          console.log(this.isShouCang);
          this.rubberFlag = true;
          setTimeout(() => {
          this.rubberFlag = false;
          }, 500);
        }
      }
    }
  };
</script>

<style scoped lang="less">
  @rpx:0.027rem;
  .test{
    width: 100%;
    height: 300px;
    background: yellow;
    padding: 20px;
    box-sizing: border-box;
  }
  .business{
    box-sizing: border-box;
    background: #dcdcdc;
    overflow: auto;
    overflow-x: hidden;
    padding-bottom: 30px;
  }
  .busHeader{
    box-sizing: border-box;
    width: 100%;
    position: relative;
    padding: 20px;
    border-bottom: 1px solid #ccc;
    background: white;
    margin-bottom: 50*@rpx;
  }
  .busStar{
    display: flex;
    flex-direction: row;
  }
  .heart{
    position: absolute;
    right: 20px;
    top: 20px;
    text-align: center;
    div{
      padding-top: 10px;
    }
  }
  .busTit{
    padding: 10px;
  }
  .shou{
    font-size: smaller;
    color: gray;
    padding-bottom: 15px;
  }
  .fa-heart{
    color: red;
    font-size: larger;
  }
  .fa-heart-o{
    color: red;
  }
  .head2{
    padding: 20px;
    display: flex;
    flex-direction: row;
    justify-content: space-around;
    align-items: center;
    div{
      text-align: center;
    }
  }
  .GonHuo{
    box-sizing: border-box;
    background: white;
    width: 100%;
    padding: 15px 30px;
    border-bottom: 1px solid #ccc;
    .ghTitle{
      font-size: larger;
      font-weight: bolder;
      padding-bottom: 10px;
    }
    .ghCont{
      padding: 0 30px 0 10px;
      font-size: smaller;
      color:#cd5c5c;
    }
  }
  .jian{
    display: inline-block;
    width: 40*@rpx;
    height: 40*@rpx;
    background: white;
    margin-right: 8px;
    img{
      width: 100%;
      height: 100%;
    }
  }
  .youhui{
    box-sizing: border-box;
    background: white;
    width: 100%;
    padding: 15px 30px;
    div{
      padding: 15px 0;
      font-size: 14px;
      padding-left: 8px;
      display: flex;
      flex-direction: row;
    }
  }
  .shijing{
    box-sizing: border-box;
    overflow: auto;
    padding: 15px;
    width: 100%;
    background: white;
    .imgGrp{
      width: 960px;
      height: 180px;
      display: flex;
      flex-direction: row;
      div{
        height: 180px;
        width: 240px;
        background: skyblue;
        margin-right: 10px;
        img{
          width: 100%;
          height: 100%;
        }
      }
    }
  }
  .jingTit{
    box-sizing: border-box;
    font-size: larger;
    padding: 15px;
    width: 100%;
    background: white;
    }
    .imformation{
      width: 100%;
      box-sizing: border-box;
      padding: 15px 30px;
      background: white;
      .imTitle{
        font-size: larger;
        font-weight: bold;
      }
      .imCont{
        font-size: small;
        color: rgb(7,17,27);
        padding:10px;
        div{
          border-bottom: 1px solid rgba(7,17,27,0.1);
          padding-bottom: 10px;
        }
      }
    }
</style>



